import * as React from "react";
import { useMemo } from "react";
import { createGlobalStyle } from "styled-components";
import { init } from "../fileSystem/init";
import { SendHostScript } from "../fileSystem/connectJSX";

import MainAIPage from "./pages/mainPage";

import connectServer from "../server/server";

const GlobalStyle = createGlobalStyle`
    body{
        margin: 0;
        font-family: "Helvetica Neue" , Helvetica , Arial , Verdana , Roboto , "" , "Yu Gothic" , "" , "YuGothic" , " Pro W3" , "Hiragino Kaku Gothic Pro" , "Meiryo UI" , "" , Meiryo , " " , "MS PGothic" , sans-serif;
        background: #e2914e;
    }
`;

const LayoutAI = () =>{
    useMemo(()=>{
        init();
        connectServer(async(actionFile)=>{
            actionFile.funcType = "AIAction";
            const jsx = new SendHostScript();
            await jsx.callHostScript(actionFile)
        },3000);
    },[]);
    return(
        <>
            <GlobalStyle />
            <MainAIPage />
        </>
    )
}

export default LayoutAI;